$font-family: 'Roboto',
'Arial',
'Helvetica',
sans-serif;
$font-weight: 400;
$font-size: 14px;
$line-height: 18px;
$font-lite: #FFFFFF;
$font-dark: #383838;
$font-blue: #0039A9;
$bg-lite: #FFFFFF;
$bg-blue: #0F2F95;
$bg-grey: #F7F9FC;
$btn-bg-red: #F5543F;
$btn-bg-red-dark: #D12E19;
$btn-bg-blue: #437FF3;
$btn-bg-blue-dark: #0039A9;
$border: #D3D3D3;
$border-blue: #0039A9;
$input: #878787;
.title--h1 {
    font-weight: 900;
    text-transform: uppercase;
    letter-spacing: 0.05em;
    font-size: 34px;
    line-height: 40px;
    @media (min-width: 768px) {
        font-size: 42px;
        line-height: 49px;
    }
    @media (min-width: 1280px) {
        font-size: 52px;
        line-height: 61px;
    }
}

.title--h2 {
    font-weight: 700;
    font-size: 24px;
    line-height: 31px;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    color: $font-blue;
    @media (min-width: 768px) {
        font-size: 36px;
        line-height: 50px;
    }
    @media (min-width: 1024px) {
        font-size: 42px;
        line-height: 59px;
    }
}

.title--h3 {
    font-weight: 700;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: $font-dark;
    font-size: 18px;
    line-height: 21px;
    @media (min-width: 768px) {
        font-size: 24px;
        line-height: 28px;
    }
    @media (min-width: 1280px) {
        font-size: 32px;
        line-height: 37px;
    }
}

.title--h4 {
    font-size: 14px;
    line-height: 16px;
    color: $font-dark;
    @media (min-width: 768px) {
        font-size: 16px;
        line-height: 19px;
    }
    @media (min-width: 1280px) {
        font-size: 18px;
        line-height: 21px;
    }
}

.text {
    font-weight: 500;
    font-size: 22px;
    line-height: 31px;
    @media (min-width: 768px) {
        font-size: 24px;
        line-height: 34px;
    }
    @media (min-width: 1280px) {
        font-size: 28px;
        line-height: 39px;
    }
}

.text-small {
    color: $font-dark;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    @media (min-width: 768px) {
        font-size: 16px;
        line-height: 21px;
    }
    @media (min-width: 1280px) {
        font-size: 18px;
        line-height: 23px;
    }
}