@import 'variables';
@import 'grid';
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: $font-family;
    font-size: $font-size;
    font-weight: $font-weight;
    line-height: $line-height;
    color: $font-dark;
    background: $bg-lite;
}

.button {
    display: block;
    text-decoration: none;
    text-align: center;
    width: 355px;
    height: 70px;
    padding: 25px;
    font-weight: 500;
    font-size: 18px;
    line-height: 21px;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    border-radius: 8px;
    &--red {
        color: $font-lite;
        background: $btn-bg-red;
        &:hover {
            background: $btn-bg-red-dark;
        }
        &:focus {
            background: $btn-bg-red-dark;
        }
        &:active {
            color: $btn-bg-red;
            background: $font-lite;
            border: 2px solid $btn-bg-red;
        }
    }
    &--blue {
        color: $btn-bg-blue-dark;
        background: $font-lite;
        border: 2px solid $btn-bg-blue-dark;
        &:hover {
            color: $btn-bg-blue;
            border: 2px solid $btn-bg-blue;
        }
        &:focus {
            color: $btn-bg-blue;
            border: 2px solid $btn-bg-blue;
        }
        &:active {
            color: $font-lite;
            background: $btn-bg-blue-dark;
            border: 2px solid $btn-bg-blue-dark;
        }
    }
}

.menu {
    display: flex;
    justify-content: space-around;
    flex-direction: row;
    position: relative;
    &__list {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-around;
        padding: 1%;
        display: none;
        @media (min-width: 270px) {
            width: 90%;
        }
        @media (min-width: 576px) {
            width: 330px;
        }
        @media (min-width: 1280px) {
            display: flex;
            width: 600px;
        }
        &--active {
            display: block;
            width: 330px;
            position: absolute;
            top: 90px;
            min-height: 370px;
            transition: linear 1s;
        }
    }
    &__item {
        list-style: none;
    }
    &__link {
        display: block;
        text-decoration: none;
        background: $bg-blue;
        color: $font-lite;
        padding: 40px 0;
        &:hover {
            padding-bottom: 7px;
            border-bottom: 3px solid $btn-bg-red;
        }
        &:focus {
            padding-bottom: 7px;
            border-bottom: 3px solid $btn-bg-red;
        }
        &:active {
            padding-bottom: 7px;
            border-bottom: 3px solid $btn-bg-red;
        }
    }
    &__burger {
        margin: 5%;
        @media (min-width: 1280px) {
            display: none;
        }
    }
    &__contact-list {
        width: 320px;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-around;
        padding: 1%;
        margin-left: 5%;
        @media (min-width: 1280px) {
            margin-left: 1%;
        }
    }
    &__item--contacts {
        display: none;
        text-decoration: none;
        background: $bg-blue;
        color: $font-lite;
        padding: 40px 0;
        @media (min-width: 568px) {
            display: block;
            width: 50%;
        }
    }
}

.header {
    background: $bg-blue;
    color: $font-lite;
    background-image: url(/img/bg__header.png);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: top left;
    padding-bottom: 160px;
    &__nav {
        background: $bg-blue;
        color: $font-lite;
        min-height: 115px;
        text-align: center;
        font-size: 18px;
        line-height: 21px;
    }
    &__logo {
        width: 100px;
        float: left;
        margin: 3%;
    }
    &__title {
        margin-top: 150px;
        @media (min-width: 568px) {
            margin-top: 40px;
            width: 430px;
        }
        @media (min-width: 768px) {
            margin-top: 60px;
            width: 585px;
        }
        @media (min-width: 1024px) {
            margin-top: 130px;
        }
        @media (min-width: 1280px) {
            margin-top: 135px;
            max-width: 724px;
        }
    }
    &__text {
        margin-top: 15px;
        max-width: 290px;
        @media (min-width: 568px) {
            margin-top: 20px;
            max-width: 425px;
        }
        @media (min-width: 768px) {
            margin-top: 35px;
            max-width: 550px;
        }
        @media (min-width: 1280px) {
            margin-top: 35px;
        }
        @media (min-width: 1920px) {
            max-width: 690px;
        }
    }
    &__button {
        margin-top: 30px;
        width: 100%;
        font-size: 14px;
        line-height: 16px;
        @media (min-width: 568px) {
            width: 330px;
        }
        @media (min-width: 768px) {
            margin-top: 35px;
        }
        @media (min-width: 1024px) {
            font-size: 16px;
            line-height: 19px;
        }
        @media (min-width: 1280px) {
            width: 357px;
            margin-top: 45px;
            font-size: 18px;
            line-height: 21px;
        }
    }
}

.provision {
    padding-bottom: 50px;
    &__item {
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        justify-content: center;
        list-style: none;
        padding: 20px;
        padding-left: 80px;
        padding-right: 5px;
        margin-bottom: 5px;
        width: 98%;
        background: $bg-grey;
        border-radius: 20px;
        background-repeat: no-repeat;
        background-position: 15px center;
        background-size: 48px;
        @media (min-width: 568px) {
            width: 535px;
            min-height: 125px;
            margin-bottom: 15px;
            padding-right: 15px;
            padding-left: 130px;
            background-position: 55px center;
            background-size: 65px;
        }
        @media (min-width: 768px) {
            width: 705px;
            min-height: 150px;
            padding-left: 215px;
            background-position: 105px center;
        }
        @media (min-width: 1024px) {
            width: 45%;
            max-width: 545px;
            margin-bottom: 25px;
            margin-right: 25px;
            padding-right: 25px;
            padding-left: 125px;
            background-position: 35px center;
        }
        @media (min-width: 1280px) {
            margin-bottom: 30px;
            margin-right: 30px;
            padding-right: 30px;
            padding-left: 155px;
            background-position: 55px center;
            background-size: 86px;
        }
        @media (min-width: 1920px) {
            max-width: 570px;
        }
        &--guarantee {
            background-image: url(/img/provision__item--guarantee.png);
        }
        &--prices {
            background-image: url(/img/provision__item--prices.png);
            @media (min-width: 1024px) {
                order: 1;
            }
        }
        &--documents {
            background-image: url(/img/provision__item--documents.png);
        }
        &--maintenance {
            background-image: url(/img/provision__item--maintenance.png);
            @media (min-width: 1024px) {
                order: 1;
            }
        }
        &--text {
            margin-top: 5px;
        }
        &--title {
            color: $font-dark;
        }
    }
    &__list {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-around;
        margin-top: 25px;
    }
    &__title {
        color: $font-blue;
        margin-top: 50px;
        @media (min-width: 568px) {
            margin-top: 55px;
        }
        @media (min-width: 768px) {
            margin-top: 80px;
        }
        @media (min-width: 1280px) {
            margin-top: 100px;
        }
        @media (min-width: 1024px) {
            margin-top: 120px;
        }
    }
}

.article {
    @media (min-width: 1024px) {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }
    padding-bottom: 20px;
    &__item {
        display: inline-block;
        vertical-align: middle;
        min-height: 35px;
        list-style: none;
        background-image: url(/img/article__item.png);
        background-repeat: no-repeat;
        background-size: 32px;
        background-position: left top;
        margin-bottom: 10px;
        padding-left: 45px;
        @media (min-width: 768px) {
            padding-left: 50px;
        }
    }
    &__title {
        color: $font-dark;
        @media (min-width: 1024px) {
            max-width: 360px;
        }
    }
    &__main {
        @media (min-width: 1024px) {
            max-width: 630px;
        }
        @media (min-width: 1280px) {
            max-width: 735px;
        }
        @media (min-width: 1920px) {
            max-width: 775px;
        }
    }
    &__list {
        color: $font-dark;
        font-weight: 700;
        font-size: 14px;
        line-height: 16px;
        margin-top: 20px;
        @media (min-width: 768px) {
            font-size: 16px;
            line-height: 19px;
            margin-top: 25px;
        }
        @media (min-width: 1024px) {
            margin-top: 0;
        }
        @media (min-width: 1280px) {
            font-size: 18px;
            line-height: 21px;
        }
    }
    &__text {
        padding-top: 10px;
    }
    &__img {
        margin: 20px 0;
        max-width: 90vw;
        @media (min-width: 568px) {
            width: 535px;
        }
        @media (min-width: 768px) {
            width: 705px;
        }
        @media (min-width: 1024px) {
            width: 630px;
        }
        @media (min-width: 1280px) {
            width: 735px;
        }
        @media (min-width: 1920px) {
            width: 775px;
        }
    }
}

.value {
    padding-bottom: 50px;
    background: $bg-grey;
    &__item {
        list-style: none;
    }
    &__list {
        margin-top: 25px;
        @media (min-width: 568px) {
            margin-top: 35px;
        }
        @media (min-width: 768px) {
            margin-top: 40px;
        }
        @media (min-width: 1024px) {
            margin-top: 55px;
        }
        @media (min-width: 1280px) {
            margin-top: 60px;
        }
    }
    &__title {
        padding-top: 50px;
        @media (min-width: 568px) {
            padding-top: 55px;
        }
        @media (min-width: 768px) {
            padding-top: 80px;
        }
        @media (min-width: 1024px) {
            padding-top: 100px;
        }
        @media (min-width: 1280px) {
            padding-top: 120px;
        }
    }
}

.contact-form {
    color: $font-lite;
    background-color: $bg-blue;
    padding-top: 50px;
    padding-bottom: 60px;
    @media (min-width: 568px) {
        padding: 55px 15px;
    }
    @media (min-width: 768px) {
        padding: 80px 60px;
    }
    @media (min-width: 1024px) {
        padding: 100px 30px;
    }
    @media (min-width: 1280px) {
        padding: 120px 80px;
    }
    @media (min-width: 1920px) {
        padding: 120px auto;
    }
    &__button {
        width: 100%;
        height: 60px;
        border: none;
        padding: 0;
        font-weight: 500;
        font-size: 14px;
        line-height: 16px;
        @media (min-width: 768px) {
            max-width: 335px;
            font-size: 16px;
            line-height: 19px;
        }
        @media (min-width: 1280px) {
            font-size: 18px;
            line-height: 21px;
        }
        &:hover {
            background: $btn-bg-red-dark;
        }
        &:focus {
            background: $btn-bg-red-dark;
        }
        &:active {
            color: $btn-bg-red;
            background: $font-lite;
            border: 2px solid $btn-bg-red;
        }
    }
    &__container {
        @media (min-width: 568px) {
            background: $bg-grey;
            border-radius: 20px;
            width: 535px;
            padding: 55px 45px;
        }
        @media (min-width: 768px) {
            width: 625px;
            padding: 60px;
        }
        @media (min-width: 1024px) {
            width: 960px;
            padding: 70px 50px 90px;
            display: flex;
            flex-direction: row;
            justify-content: space-around;
        }
        @media (min-width: 1280px) {
            width: 1120px;
            padding: 70px 60px 85px;
        }
        @media (min-width: 1920px) {
            width: 1170px;
        }
        &--questions {
            display: block;
            background: transparent;
            // @media (min-width: 768px) {
            //     width: 450px;
            //     margin: 0;
            //     margin-left: auto;
            //     padding: 10px;
            // }
        }
    }
    &__text-wrapper {
        @media (min-width: 1024px) {
            width: 485px;
        }
        @media (min-width: 1280px) {
            width: 550px;
        }
        &--questions {
            @media (min-width: 568px) {
                width: 420px;
            }
            @media (min-width: 768px) {
                width: 520px;
                margin: 0;
                margin-left: auto;
                padding: 10px;
            }
        }
    }
    &__form-wrapper {
        width: 300px;
        @media (min-width: 1024px) {
            width: 335px;
        }
        @media (min-width: 1280px) {
            width: 370px;
        }
        &--questions {
            @media (min-width: 568px) {
                width: 420px;
            }
            @media (min-width: 768px) {
                width: 520px;
                margin: 0;
                margin-left: auto;
                padding: 10px;
            }
        }
    }
    &__input {
        min-height: 60px;
        background: $bg-lite;
        border: 1px solid $border;
        border-radius: 8px;
        font-weight: 400;
        color: $input;
        padding-left: 15px;
        width: 100%;
        font-size: 14px;
        line-height: 18px;
        @media (min-width: 1024px) {
            font-size: 16px;
            line-height: 21px;
        }
        @media (min-width: 1280px) {
            font-size: 18px;
            line-height: 23px;
        }
        &:hover,
        &:focus,
        &:active {
            background: $bg-grey;
            border: 3px solid $border-blue;
            color: $font-blue;
        }
        &:focus~.contact-form__label {
            display: block;
            top: 3px;
            left: 25px;
            color: $input;
        }
    }
    &__input-wrapper {
        position: relative;
        margin-bottom: 10px;
        width: 100%;
        @media (min-width: 568px) {
            width: 100%;
        }
        @media (min-width: 768px) {
            max-width: 335px;
            margin-bottom: 15px;
        }
    }
    &__label {
        display: none;
        position: absolute;
    }
    &__text {
        margin: 25px 0;
        &--questions {
            color: $font-lite;
        }
    }
    &__title {
        font-weight: 900;
        @media (min-width: 568px) {
            color: $font-blue;
        }
        @media (min-width: 1024px) {
            padding-top: 20px;
        }
        &--questions {
            color: $font-lite;
        }
    }
    &--information {
        background-image: url(/img/bg__contact-form--information--320.png);
        background-repeat: no-repeat;
        background-position: left top;
        background-size: cover;
    }
    &--questions {
        color: $font-lite;
        background: $bg-blue;
        background-image: url(/img/bg__contact-form--question.png);
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        padding-right: 0;
    }
}

