.steps {
    &__title {
        margin-top: 5px;
    }
    &__list {
        list-style: none;
        margin-top: 25px;
        margin-bottom: 50px;
        @media (min-width: 568px) {
            margin-top: 35px;
        }
        @media (min-width: 768px) {
            margin-top: 40px;
            margin-bottom: 80px;
        }
        @media (min-width: 1024px) {
            margin-top: 55px;
            margin-bottom: 100px;
        }
        @media (min-width: 1280px) {
            margin-top: 60px;
            margin-bottom: 120px;
        }
    }
    &__item {
        background: $bg-grey;
        border-radius: 20px;
        width: 98%;
        padding: 25px 15px;
        padding-left: 75px;
        margin-bottom: 10px;
        background-repeat: no-repeat;
        background-size: 42px;
        background-position: 15px 20px;
        z-index: 1;
        position: relative;
        &:first-child::before,
        &:last-child::after {
            display: none;
        }
        &::before,
        &::after {
            content: "";
            width: 2px;
            height: 40%;
            position: absolute;
            top: -30%;
            left: 35px;
            background-color: $font-blue;
            z-index: 1;
            @media (min-width: 568px) {
                left: 70px;
                top: -27%;
            }
            @media (min-width: 768px) {
                top: -25%;
            }
            @media (min-width: 1024px) {
                left: 90px;
            }
            @media (min-width: 1280px) {
                left: 122px;
                top: -20%;
            }
        }
        &::after {
            height: 100%;
            top: 60px;
            @media (min-width: 568px) {
                top: 80px;
            }
            @media (min-width: 1024px) {
                top: 88px;
            }
            @media (min-width: 12804px) {
                top: 90px;
            }
        }
        @media (min-width: 568px) {
            max-width: 535px;
            padding: 30px;
            padding-left: 150px;
            margin-bottom: 15px;
            background-size: 56px;
            background-position: 40px 25px;
        }
        @media (min-width: 768px) {
            max-width: 705px;
            padding: 45px 30px;
            padding-left: 140px;
            margin-bottom: 30px;
            display: flex;
            flex-direction: row;
