            max-width: 1170px;
            padding: 40px 70px;
            padding-left: 250px;
            background-position: 90px 25px;
        }
        &--1 {
            background-image: url(/img/steps__item--1.png);
        }
        &--2 {
            background-image: url(/img/steps__item--2.png);
        }
        &--3 {
            background-image: url(/img/steps__item--3.png);
        }
        &--4 {
            background-image: url(/img/steps__item--4.png);
        }
        &--5 {
