        margin-right: 15px;
        @media (min-width: 568px) {
            width: 320px;
        }
        @media (min-width: 768px) {
            width: 410px;
        }
        @media (min-width: 1024px) {
            width: 600px;
        }
        @media (min-width: 1280px) {
            width: 680px;
        }
        @media (min-width: 1920px) {
