.example {
    background: $bg-grey;
    padding: 50px 0;
    &__title {
        margin: 5px 0;
    }
    &__img {
        margin: 25px 5px;
        width: 98%;
        border: 2px solid $font-blue;
        border-radius: 25px;
    }
    &__wrapper {
        @media (min-width: 1024px) {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
        }
    }
    &__offer {
        margin: 25px 0;
        @media (min-width: 1024px) {
            width: 45%;
        }
    }
    &__input-wrapper {
        position: relative;
        margin-bottom: 10px;
        width: 98%;
        @media (min-width: 568px) {
            display: flex;
            flex-direction: row;
            margin: 25px 0;
            max-width: 460px;
        }
        @media (min-width: 1024px) {
            width: 55%;
            padding-bottom: 10px;
            height: 58px;
            margin-top: 5%;
            max-width: 600px;
        }
    }
    &__email {
        min-height: 60px;
        background: $bg-lite;
        border: 1px solid $border;
        border-radius: 8px;
        color: $input;
        padding-left: 15px;
        width: 100%;
        &:hover,
        &:focus,
        &:active {
            background: $bg-grey;
            border: 3px solid $border-blue;
            color: $font-blue;
        }
        &:focus~.example__label {
            display: block;
            top: 3px;
            left: 25px;
            color: $input;
        }
        @media (min-width: 568px) {
            width: 60%;
            border-top-right-radius: 0;
            border-bottom-right-radius: 0;
        }
    }
    &__label {
        display: none;
        position: absolute;
    }
    &__button {
        display: block;
        width: 98%;
        height: auto;
        padding: 20px;
        font-size: 14px;
        line-height: 16px;
        margin-top: 15px;
        @media (min-width: 568px) {
            width: 40%;
            border-top-left-radius: 0;
            border-bottom-left-radius: 0;
            margin-top: 0;
        }
        @media (min-width: 1024px) {
            height: 60px;
        }
    }
}

.cases {
    padding: 50px 0;
    &__title {
        padding: 1px;
    }
    &__list {
        margin: 25px 0;
        margin-bottom: 10px;
    }
    &__item {
        list-style: none;
        position: relative;
        border-radius: 10px;
    }
    &__img {
        width: 300px;
        height: 255px;
        border-radius: 10px;
        @media (min-width: 568px) {
            width: 250px;
            height: 215px;
        }
        @media (min-width: 768px) {
            width: 350px;
            height: 300px;
        }
    }
    &__label {
        font-weight: 500;
        font-size: 22px;
        line-height: 26px;
        color: $font-lite;
        position: absolute;
        bottom: 30px;
        left: 10px;
        max-width: 290px;
    }
}

.customers {
    padding: 50px 0;
    &__title {
        padding: 1px;
    }
    &__list {
        margin: 25px 0;
        margin-bottom: 10px;
    }
    &__item {
        list-style: none;
        position: relative;
        border-radius: 10px;
    }
}

.slider {
    position: relative;
    width: 300px;
    margin: 0 auto;
    @media (min-width: 568px) {
        width: inherit;
    }
    &__radio {
        display: none;
    }
    &__labels {
        position: absolute;
        left: 0;
        bottom: -30px;
        text-align: center;
        width: 100%;
        @media (min-width: 568px) {
            display: none;
        }
    }
    &__label {
        display: inline-block;
        width: 18px;
        height: 18px;
        cursor: pointer;
        margin: 0 3px;
        border-radius: 50%;
        border: 2px solid $font-blue;
        background-color: $bg-lite;
        @media (min-width: 568px) {
            display: none;
        }
    }
    &__content {
        overflow: hidden;
        @media (min-width: 568px) {
            overflow: visible;
        }
    }
    &__slides {
        display: flex;
        width: 100%;
        transition: all 0.5s;
        @media (min-width: 568px) {
            overflow: visible;
            flex-direction: row;
            flex-wrap: wrap;
            justify-content: space-around;
            gap: 30px;
            margin: 50px auto;
        }
    }
    &__slide {
        width: 100%;
        flex-shrink: 0;
        @media (min-width: 568px) {
            width: auto;
        }
    }
}

#slide1:checked~.slider__labels .slider__label[for="slide1"],
#slide2:checked~.slider__labels .slider__label[for="slide2"],
#slide3:checked~.slider__labels .slider__label[for="slide3"],
#slide4:checked~.slider__labels .slider__label[for="slide4"],
#slide5:checked~.slider__labels .slider__label[for="slide5"],
#slide6:checked~.slider__labels .slider__label[for="slide6"],
#slide7:checked~.slider__labels .slider__label[for="slide7"],
#slide8:checked~.slider__labels .slider__label[for="slide8"],
#customers__slide1:checked~.slider__labels .slider__label[for="customers__slide1"],
#customers__slide2:checked~.slider__labels .slider__label[for="customers__slide2"],
#customers__slide3:checked~.slider__labels .slider__label[for="customers__slide3"],
#customers__slide4:checked~.slider__labels .slider__label[for="customers__slide4"] {
    background: $font-blue;
}

#slide1:checked~.slider__content .slider__slides,
#customers__slide1:checked~.slider__content .slider__slides {
    transform: translate(0);
}

#slide2:checked~.slider__content .slider__slides,
#customers__slide2:checked~.slider__content .slider__slides {
    transform: translateX(-100%);
}

#slide3:checked~.slider__content .slider__slides,
#customers__slide3:checked~.slider__content .slider__slides {
    transform: translateX(-200%);
}

#slide4:checked~.slider__content .slider__slides,
#customers__slide4:checked~.slider__content .slider__slides {
    transform: translateX(-300%);
}

#slide5:checked~.slider__content .slider__slides {
    transform: translateX(-400%);
}

#slide6:checked~.slider__content .slider__slides {
    transform: translateX(-500%);
}

#slide7:checked~.slider__content .slider__slides {
    transform: translateX(-600%);
}

#slide8:checked~.slider__content .slider__slides {
    transform: translateX(-700%);
}

